<template>
  <div class="details">
    <!-- 导航栏 -->
    <van-nav-bar title="详情页" placeholder fixed border>
      <template #left>
        <van-icon name="cross" size="26" color="white" @click="backPage()" />
      </template>
    </van-nav-bar>
    <div v-if=" isShow">
      <div class="details-content">
        <!-- 标题 -->
        <div class="datails-title">
          <h1>{{detailData.title}}</h1>
        </div>

        <!-- 用户 -->
        <div class="datails_user">
          <div class="user-img">
            <img :src="detailData.media_user.avatar_url" />
          </div>
          <div class="user-name">{{detailData.media_user.screen_name}}</div>
        </div>

        <!-- 内容 -->
        <div class="details-html" v-html="detailData.content"></div>
      </div>
    </div>
    <div v-else>
      <div v-if="isData">
        <van-empty image="error" description="没有数据" />
        <div class="url-btn">
          <van-button type="default" :url="details_url">查看原文</van-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "@/assets/css/details.less";

// 引入 mapState 插件
import { mapState } from "vuex";

export default {
  data() {
    return {
      isShow: false,

      isData: false,

      // 详情
      detailData: {
        media_user: {}
      }
    };
  },
  created() {
    // 获取详情页数据
    this.getDetailData();
  },
  computed: {
    ...mapState(["item_id", "details_url"])
  },
  methods: {
    // 详情
    getDetailData() {
      // 打开加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        // url: "https://berkelium-1.github.io/jsons/detail.json",

        url: "/detail",
        params: {
          apikey: this.apikey,
          itemId: this.item_id
        }
      })
        .then(res => {
          this.$toast.clear();

          if (res.data.retcode == "0000") {
            this.$toast.clear();
            this.detailData = res.data.data;

            // 显示内容
            this.isShow = true;
          } else {
            // 跳转到原网页
            location.href = url;
            // this.$toast.fail("服务器出了点问题 x——x");
            // 显示空状态
            // this.isData = true;
          }
        })
        .catch(err => {
          location.href = url;

          // 显示空状态
          // this.isData = true;
          // this.$toast.fail("加载失败");
        });
    },

    // 返回页面
    backPage() {
      this.$router.back(-1);
    }
  }
};
</script>
